.stepsBlock {
    background: #fff;
    padding-top: 100px;
    padding-bottom: 50px;
}
@media all and (max-width: 980px) {
    .stepsBlock {
        padding: 22px;
        padding-bottom: 20px;
    }
}
.stepsBlock__header {
    margin-bottom: 66px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    gap: 16px;
}
@media all and (max-width: 980px) {
    .stepsBlock__header {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        gap: 10px;
        margin-bottom: 30px;
        -webkit-box-align: start;
            -ms-flex-align: start;
                align-items: flex-start;
    }
}
.stepsBlock__desc {
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;
    color: var(--grey-2);
}
.stepsBlock__list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    gap: 48px;
}
@media all and (max-width: 550px) {
    .stepsBlock__list {
        gap: 30px;
    }
}
.stepsBlock__item {
    width: calc(33.3% - 32px);
}
@media all and (max-width: 768px) {
    .stepsBlock__item {
        width: calc(50% - 24px);
    }
}
@media all and (max-width: 550px) {
    .stepsBlock__item {
        width: 100%;
    }
}
.stepsBlock__item:last-child .stepsBlock__item-title:before {
    background-image: url("data:image/svg+xml,%3Csvg width='89' height='2' viewBox='0 0 89 2' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline opacity='0.3' x1='0.0993661' y1='0.991211' x2='88.991' y2='0.991364' stroke='black'/%3E%3C/svg%3E%0A");
}
.stepsBlock__itemHeader {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    gap: 15px;
    margin-bottom: 40px;
}
.stepsBlock__itemNumber {
    font-family: "helvetica", sans-serif;
    font-size: 26px;
    font-weight: 700;
    line-height: 39px;
    text-align: left;
    color: var(--primary);
    position: relative;
}
.stepsBlock__itemNumber span {
    position: relative;
}
.stepsBlock__itemCircle {
    position: absolute;
    top: -16px;
    left: -14px;
    width: 0;
    height: 0;
    border-radius: 100%;
    background: #F3EFEB;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
.stepsBlock__itemTitle {
    font-family: "helvetica", sans-serif;
    font-size: 24px;
    font-weight: 700;
    line-height: 32px;
    text-align: left;
    color: var(--black);
    position: relative;
}
.stepsBlock__itemTitle span {
    font-family: inherit;
}
.stepsBlock__itemTitle ins {
    position: absolute;
    left: 0;
    right: 0;
    bottom: -13px;
    width: 89px;
    height: 8px;
    font-size: 0;
    line-height: normal;
}
.stepsBlock__itemList {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    gap: 13px;
}
.stepsBlock__itemList .listItem {
    padding-left: 36px;
    position: relative;
}
.stepsBlock__itemList .listItem:before {
    content: "";
    width: 11.5px;
    height: 11.5px;
    position: absolute;
    left: 0;
    top: 7px;
    border-radius: 100%;
    background: var(--primary);
}
.stepsBlock__itemList .listItem__title {
    font-family: "helvetica", sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    text-align: left;
    color: var(--black-blue);
}
.stepsBlock__itemList .listItem__desc {
    margin-top: 13px;
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;
    text-align: left;
    color: var(--grey-2);
}