.problemsBlock {
    padding-top: 100px;
    padding-bottom: 48px;
}
@media all and (max-width: 980px) {
    .problemsBlock {
        padding-top: 20px;
    }
}
.problemsBlock__inner {
    width: 100%;
    max-width: calc((100% + 1320px) / 2);
    margin-left: auto;
    padding-right: 28px;
    margin-right: 0;
}
@media all and (max-width: 980px) {
    .problemsBlock__inner {
        max-width: 100%;
        padding-right: 0;
    }
}
.problemsBlock__top {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    gap: 24px;
    margin-bottom: 20px;
    max-width: 1320px;
}
@media all and (max-width: 980px) {
    .problemsBlock__top {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        gap: 10px;
    }
}
.problemsBlock__desc {
    font-family: "helvetica-light", sans-serif;
    font-size: 1rem;
    font-weight: 400;
    line-height: 26px;
    text-align: left;
    color: #6A6A6A;
    max-width: 725px;
}
.problemsBlock__list {
    position: relative;
    z-index: 1;
}
@media all and (max-width: 980px) {
    .problemsBlock__list {
        width: calc(100% + 20px);
        margin-right: -20px;
    }
}
.problemsBlock__list-slider {
    padding: 30px 12px 55px;
    margin-left: -12px;
    position: relative;
}
.problemsBlock__item {
    padding: 20px 27px;
    background: #fff;
    border-radius: 5px 8px 5px 5px;
    -webkit-box-shadow: 0px 8px 15px 0px rgba(0, 0, 0, 0.1882352941);
            box-shadow: 0px 8px 15px 0px rgba(0, 0, 0, 0.1882352941);
    position: relative;
    height: auto;
}
@media all and (max-width: 980px) {
    .problemsBlock__item {
        padding: 20px 22px;
    }
}
.problemsBlock__itemTitle {
    font-family: "helvetica", sans-serif;
    font-size: 1.25rem;
    font-weight: 700;
    line-height: 30px;
    text-align: left;
    color: var(--black-blue);
    margin-bottom: 20px;
    padding-right: 23px;
}
.problemsBlock__itemIcon {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    font-size: 0;
    width: 48px;
    height: 48px;
    border-bottom: 8px solid #FAF6F3;
    border-left: 8px solid #FAF6F3;
    border-radius: 0 8px 0 8px;
    background: #FAF6F3;
}
.problemsBlock__itemIcon span {
    width: 40px;
    height: 40px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    font-size: 0;
    background: var(--primary);
    border-radius: 8px;
}
.problemsBlock__itemProblemTitle {
    padding: 2px 7px;
    border-radius: 7px;
    background: #D9F4FC;
    display: inline-block;
    width: auto;
    margin-bottom: 10px;
    color: var(--black);
    font-family: "Geologica", sans-serif;
    font-size: 0.9375rem;
    font-weight: 250;
    line-height: 15px;
    text-align: left;
}
.problemsBlock__itemProblemText {
    font-size: 1rem;
    font-weight: 400;
    line-height: 26px;
    text-align: left;
    color: var(--black);
}
.problemsBlock__itemImpact {
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid #F3EFEB;
}
.problemsBlock__itemImpactTitle {
    font-family: "helvetica", sans-serif;
    font-size: 1rem;
    font-weight: 700;
    line-height: 24px;
    text-align: left;
    color: var(--black);
}
.problemsBlock__itemImpactText {
    margin-top: 10px;
    font-size: 1rem;
    font-weight: 400;
    line-height: 26px;
    text-align: left;
    color: var(--grey-2);
}
.problemsBlock__checkText {
    font-size: 1rem;
    font-weight: 400;
    line-height: 26px;
    text-align: left;
    color: var(--grey-2);
    position: relative;
    max-width: 725px;
    margin-top: -20px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    gap: 20px;
}
.problemsBlock__checkText .icon {
    position: relative;
    width: 40px;
    margin-top: 4px;
}
.problemsBlock__checkText .icon img {
    max-width: 40px;
}
.problemsBlock__checkText:before {
    content: "";
    position: absolute;
    top: -115px;
    bottom: -48px;
    right: -232px;
    background: #F3EFEB;
    border-radius: 0 0 250px 0;
    width: 999999px;
}
@media all and (max-width: 1024px) {
    .problemsBlock__checkText:before {
        right: -70px;
    }
}
@media all and (max-width: 980px) {
    .problemsBlock__checkText:before {
        top: -65px;
        right: -20px;
        border-radius: 0 0 150px 0;
    }
}
.problemsBlock__checkText .text {
    position: relative;
}
@media all and (max-width: 980px) {
    .problemsBlock__sliderControls {
        display: none;
    }
}
.problemsBlock__sliderControls div {
    width: 47px;
    height: 47px;
    border-radius: 5px;
    background: rgba(123, 123, 123, 0.1);
    cursor: pointer;
    pointer-events: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    position: absolute;
    top: calc(50% - 23px);
    -webkit-transform: none;
            transform: none;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    z-index: 3;
}
.problemsBlock__sliderControls div:before {
    content: "";
    width: 32px;
    height: 32px;
    display: inline-block;
